    import {
        mapState,
        mapMutations
    } from 'vuex'

    export default {
		components: {},
		data(){
			return{
				id:1,
				userName:'',
				nicheng:'还未设置昵称',
				userText:'这里是个性签名'
			}
		},
		onLoad(){
			var name = this.$store.state.userName;
			this.userName = name;
		},
        computed: {
            ...mapState(['hasLogin', 'forcedLogin'])
        },
        methods: {
			
            ...mapMutations(['logout']),
            bindLogin() {
                uni.navigateTo({
                    url: '../login/login',
                });
            },
            bindLogout() {
                this.logout();
                uni.reLaunch({
                    url: '../login/login',
                });
            }
        }
    }
